* {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

hr {
  border: 0;
  border-bottom: 1px solid #eee;
  margin: 10px 0;
}

img {
  max-width: 100%;
  height: auto;
}
.App {
  margin: 0 auto;
  max-width: 1100px;
  padding-left: 10px;
  padding-right: 10px;
  word-wrap: break-word;
}

.App-menu {
  margin: 15px 0 20px;
}


.Compass {
  height: 200px;
  position: relative;
  text-align: center;
}

.Compass-background {
  border-radius: 200px;
  border: 3px solid black;
  height: 200px;
  position: absolute;
  width: 200px;
}

.Compass-cache {
  color: red;
  height: 200px;
  position: absolute;
  width: 200px;
}

